import "./Header.css"
import { Container, Navbar, Nav, NavDropdown } from 'react-bootstrap';
import { useWindowSize } from "../../Hooks/useWindowSize";
import {  Link } from "react-router-dom";
import logo from "../../images/logo.png"
function Header() {
  const size = useWindowSize();

  const w = size.width;
  const h = size.height;
  return (
    <Navbar collapseOnSelect expand="md" sticky="top" variant="dark" >
      <Container style={{width:w}}>

        <Navbar.Brand as={Link} to="/home" style={{ color: "#ffffcc"}}>
          <img alt="Encore Hakka" src={logo} />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ms-auto" >
            <Nav.Link as={Link} to="gallery" style={{ color: "#ffffcc" }}>Gallery</Nav.Link>
            <NavDropdown title="Menu" id="collapsible-nav-dropdown"  style={{color: "#ffffcc"}}>
              <NavDropdown.Item as={Link} to="lunch" >Lunch Special</NavDropdown.Item>
              <NavDropdown.Item as={Link} to="combos"  >Combos</NavDropdown.Item>
              <NavDropdown.Item as={Link} to="trays"  >Party Trays</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item as={Link} to="menu">Full Menu</NavDropdown.Item>
            </NavDropdown>
            <Nav.Link as={Link} to="coupons" style={{ color: "#ffffcc" }}>Coupons</Nav.Link>
            <Nav.Link as={Link} to="contact" style={{ color: "#ffffcc" }}>Contact</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Header;
